import { h, render } from 'preact';
import Router from './views/Router';
import './global.scss';
var node;
var start = function () {
    node = render(h(Router, null), document.body, node);
};
if (module.hot) {
    module.hot.accept('./Router', function () { return requestAnimationFrame(start); });
}
if (DEBUG) {
    import('preact/debug');
}
start();
